import { setTimeout } from 'timers';
import { eventBus } from '../main'


export default {
  name: 'Home',
  components: {
    
  },
  
  data() {
    return {
      menu: false,
      dropdownClass1: false,      
      dropdownClass2: false,
      dropdownClass3: false,      
      dropdownClass4: false,
      currentPhoto: 0,
      photos: [require('../assets/galleryImages/image1.jpg'), require('../assets/galleryImages/image2.jpg'), require('../assets/galleryImages/image3.jpg'), require('../assets/galleryImages/image4.jpg'), require('../assets/galleryImages/image5.jpg'), require('../assets/galleryImages/image6.jpg'), require('../assets/galleryImages/image7.jpg'), require('../assets/galleryImages/image8.jpg'), require('../assets/galleryImages/image9.jpg'), require('../assets/galleryImages/image10.jpg'), require('../assets/galleryImages/image11.jpg'), require('../assets/galleryImages/image12.jpg')],
