      piwa: [ {nazwa: 'Piwo', opis: "Dobre piwo z czekoladą", cena: '15.99zł', detailedDesc: 'Tutaj jest jakiś fachowy opis tego piwa i czytając go czujemy, że picie to nie jest tylko rozrywka dla przeciętniaków, mająca wypełnić im pustkę życia, z której nawet nie zdają sobie sprawy, bo nie wiedzą nawet co z tym życiem zrobić, a szlachetna sztuka degustacji, przy której somalierstwo może się schować i nie wychodzić.', imgSource: require('../assets/descPhotos/descPhoto.jpg')}, {nazwa: 'Pisswasser', opis: "Dobre piwo z piwem, czy coś", cena: '15.99zł', detailedDesc: 'Tutaj jest jakiś fachowy opis tego piwa i czytając go czujemy, że picie to nie jest tylko rozrywka dla przeciętniaków, mająca wypełnić im pustkę życia, z której nawet nie zdają sobie sprawy, bo nie wiedzą nawet co z tym życiem zrobić, a szlachetna sztuka degustacji, przy której somalierstwo może się schować i nie wychodzić.', imgSource: require('../assets/descPhotos/descPhoto.jpg')}, {nazwa: 'Scheissenwurst', opis: "Możesz wypić to albo Perłę, skutek będzie taki sam", cena: '15.99zł', detailedDesc: 'Tutaj jest jakiś fachowy opis tego piwa i czytając go czujemy, że picie to nie jest tylko rozrywka dla przeciętniaków, mająca wypełnić im pustkę życia, z której nawet nie zdają sobie sprawy, bo nie wiedzą nawet co z tym życiem zrobić, a szlachetna sztuka degustacji, przy której somalierstwo może się schować i nie wychodzić.', imgSource: require('../assets/descPhotos/descPhoto.jpg')} ],
     
    }
  },
  methods: {
    
    openMenu() {
      this.menu = !this.menu;
      
    },
    nextPhoto() {
      this.imageEnters = true;
      if (this.currentPhoto >= this.photos.length-1) {
        this.currentPhoto = 0;
      } else {this.currentPhoto++;}
      setTimeout(() => {this.imageEnters = false;}, 300)
      
    },
    previousPhoto() {
      this.imageEnters = true;
      if (this.currentPhoto <= 0) {
        this.currentPhoto = this.photos.length-1;
      } else {this.currentPhoto--;}
       setTimeout(() => {this.imageEnters = false;}, 200)
    },
    updateSelection(elementNum) {
      
      eventBus.beerDetails = this.piwa[elementNum];
      
    }
    
  }
}
</script>

